import { Component, OnInit } from '@angular/core';
import { DadesService } from '../services/dades.service'

@Component({
  selector: 'app-janssen',
  templateUrl: './janssen.page.html',
  styleUrls: ['./janssen.page.scss'],
})
export class JanssenPage implements OnInit {
  public vacunesP: Array<any>;
  public donesP: number = 0;
  public homesP: number = 0;
  public numtotalP: number = 0;
  public dosiHP: number = 0;
  public dosiDP = 0;

  constructor(private dades: DadesService) { }

    ngOnInit() {
    this.dades. dadesVacunes().subscribe(
      (data: Array<any>) => {
        
        //Recollir dades i guardarles a les propietats totals
        this.vacunesP = data;

        this.vacunesP.forEach(element => {  

          if(element.fabricant == "J&J / Janssen"){
            console.log(element.fabricant)
            this.numtotalP = +this.numtotalP+ +element.recompte
            if(element.sexe == "Home"){
              console.log(element.fabricant)
              this.homesP = +this.homesP +1
              this.dosiHP = +this.dosiHP + +element.dosi
              
            }else{
              console.log(element.fabricant)
       
              this.donesP = +this.donesP +1
              this.dosiDP = +this.dosiDP + +element.dosi
              

            }
          }
      
        });
      }
    )
    }
  }


